import { createEvent, createStore, forward, guard, sample } from 'effector';
const checkFieldPattern = (e, fieldValuePattern) => {
    if (!fieldValuePattern)
        return true;
    const valuePatterns = {
        letters: /^\w+$/,
        numbers: /^\d+$/,
    };
    const pattern = new RegExp(valuePatterns[fieldValuePattern], 'giu');
    return pattern.test(e.target.value);
};
// TODO add patronum debounce to field validation
export const createField = ({ isRequired = false, validators = [], defaultValue, name, validateOn = 'change', fieldRequiredErrorText = 'ÐŸÐ¾Ð»Ðµ Ð¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾ Ð´Ð»Ñ Ð·Ð°Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ñ', fieldValuePattern, }) => {
    const $fieldValue = createStore(defaultValue || '');
    const $fieldIsTouched = createStore(false);
    const $fieldError = createStore(null);
    const $hasError = $fieldError.map((error) => error !== null);
    const $isValid = $fieldValue.map((value) => !(!value.length && isRequired));
    const createFieldValidation = (value, touched) => ({
        isRequired: touched && isRequired && value.length === 0,
        hasValidator: touched && Boolean(validators.length),
    });
    const onFieldChanged = createEvent(`${name}Changed`);
    const onFieldFocused = createEvent(`${name}Focused`);
    const onFieldBlurred = createEvent(`${name}Blurred`);
    const onFieldTouched = createEvent(`${name}Touched`);
    const validate = createEvent();
    $fieldValue.on(guard({
        source: onFieldChanged,
        filter: (value) => checkFieldPattern(value, fieldValuePattern),
    }), (_, e) => e.currentTarget.value);
    $fieldIsTouched.on(onFieldTouched, (touched) => {
        if (touched)
            return;
        return !touched;
    });
    forward({
        from: onFieldFocused,
        to: onFieldTouched,
    });
    forward({
        from: {
            change: onFieldChanged,
            blur: onFieldBlurred,
            focus: onFieldFocused,
            formSubmit: createEvent(),
        }[validateOn],
        to: validate,
    });
    sample({
        source: [$fieldValue, $fieldIsTouched],
        clock: validate,
        fn: ([value, isTouched]) => {
            const validation = createFieldValidation(value, isTouched);
            if (validation.isRequired)
                return fieldRequiredErrorText;
            if (validation.hasValidator) {
                for (const element of validators) {
                    if (element(value))
                        return element(value);
                }
                return null;
            }
            return null;
        },
        target: $fieldError,
    });
    sample({
        source: $fieldValue,
        clock: onFieldBlurred,
    });
    return {
        name,
        type: 'field',
        value: $fieldValue,
        isTouched: $fieldIsTouched,
        isRequired,
        isValid: $isValid,
        error: $fieldError,
        hasError: $hasError,
        triggers: {
            validate,
        },
        handlers: {
            onChange: onFieldChanged,
            onFocus: onFieldFocused,
            onBlur: onFieldBlurred,
        },
    };
};
